/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin eltdfWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 0;
    width: auto;
    height: 60px;
    margin: 0;
    padding: 0 14px;
    color: #000;
    font-family: $default-text-font;
    font-weight: 400;
    font-size: 11px;
    text-transform: uppercase;
    line-height: 58px;
    letter-spacing: 0.1em;
    text-align: center;
    z-index: 100;
    box-sizing: border-box;
    @include eltdfTransform(translateZ(0));
}

@mixin eltdfWooCommerceSaleStyle() {
    right: 0;
    background-color: #fff;
}

@mixin eltdfWooCommerceOutOfStockStyle() {
    right: 0;
    background-color: #fff;
}


@mixin eltdfWooCommerceNewProductStyle() {
    right: 0;
    background-color: #fff;
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin eltdfWooCommercePriceStyle() {
    position: relative;
    display: block;
    margin: 12px 0 0;
    line-height: 1;
    color: $default-text-color;
    
    * {
        font-size: 16px;
        line-height: inherit;
    }
    
    del {
        margin: 0 13px 0 0;
       
    }
    
    ins {
        text-decoration: none;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: 'Linearicons-Free';
    font-size: 15px;
    line-height: 1;
    letter-spacing: 2px;
    color: $first-main-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin eltdfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\e814\e814\e814\e814\e814";
        opacity: 0.5;
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            content: "\e814\e814\e814\e814\e814";
        }
    }
}

@mixin eltdfWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        content: "\e814";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
        opacity: 0.5;
        top: -2px;
    }
    
    &.active:after {
        content: "\e814\e814\e814\e814\e814";
        position: absolute;
        right: 0;
        bottom: 7px;
        display: block;
        @extend %star-ratings;
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */