/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .eltdf-content,
div.woocommerce {

    .eltdf-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        position: relative;
        display: inline-block;
        vertical-align: middle;
        border: 1px solid #d7d7d7;
        box-sizing: border-box;
        line-height: 58px;
        height: 65px;
        padding: 0 22px;

        .eltdf-quantity-label {
            font-family: $default-text-font;
            font-weight: 400;
            font-size: 14px;
            margin-right: 57px;
            display: inline-block;
            vertical-align: middle;
            color: $default-text-color;;
            background-color: transparent;
            text-align: center;
            border: 0;
            box-sizing: border-box;

        }

        .eltdf-quantity-input{
            font-style: normal;
            color: $default-text-color;;
            font-size: 14px;
            border: 1px solid transparent;
            margin: 0;
        }

        span{
            &.eltdf-quantity-plus,
            &.eltdf-quantity-minus{
                color: $default-text-color;
                font-size: 13px;
            }
        }
        .eltdf-quantity-input,
        .eltdf-quantity-minus,
        .eltdf-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-text-color;;
            background-color: transparent;
            text-align: center;
            border: 0;
            box-sizing: border-box;
        }

        .eltdf-quantity-input {
            position: relative;
            width: 31px;
            padding: 0;
            margin: 0;
            font-family: inherit;
            outline: 0;
        }

        .eltdf-quantity-minus,
        .eltdf-quantity-plus {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            height: 22px;
            line-height: 22px;
            border-left: 0;
            cursor: pointer;
            @include eltdfTransition(color .15s ease-in-out);

            &:hover {
                color: $first-main-color;
            }
        }
    }
}

.woocommerce-page .eltdf-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
    }

    .coupon {

        input[type="text"] {
            width: 200px;
            height: 65px;
            margin: 0 15px 0 0;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */