/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    $thumb: 80px;
    $inner-padding: 15px; // distance between image and content
    $widget-list-item-padding: 15px; // same as sidebar vars
    $widget-border-color: #ece8e5;
    $widget-title-margin: 7px;

    clear: both;


    
    li {
        position: relative;

        a, 
        .count {
            font-size: 16px;
        }

    }



    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // title

    .product-title {
        @include eltdfH5();
    }
    .eltdf-widget-title-holder {

        .eltdf-widget-title {

            margin-bottom: $widget-title-margin !important;

        }

    }
    

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // star rating

    li:not(.wc-layered-nav-rating) {
        .star-rating {
            margin-bottom: 7px;
            margin-top: 7px;

            span {
                max-width: 100px;
            }
        }
    }

    li.wc-layered-nav-rating {
        a {
            text-decoration: none;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price

    .amount {
        color: inherit;

        span { // currency symbol
            color: inherit;
        }
    }

    del,
    ins {
        display: block;

        // currency symbol
        span {
            color: inherit;
        }
    }

    ins {
        text-decoration: none;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // layered nav

    &.widget_layered_nav {
        .chosen {
            a {
                color: $first-main-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price filter

    &.widget_price_filter {
        form {
            margin-top: 20px;
        }

        .price_slider_wrapper {
            .ui-widget-content {
                background-color: #c8c8c8;
                height: 1px;
                position: relative;

                .ui-slider-handle {
                    background-color: $first-main-color;
                    cursor: pointer;
                    width: 7px;
                    height: 7px;
                    margin: 0 0 0 -1px;
                    outline: medium none;
                    position: absolute;
                    top: -3px;
                    z-index: 2;

                    &:last-child {
                        @include eltdfTransform(translateX(-50%));
                    }
                }

                .ui-slider-range {
                    background-color: $default-text-color;
                    display: block;
                    height: 1px;
                    position: absolute;
                    top: 0;
                    z-index: 1;
                }
            }
        }

        .price_slider_amount {
            margin: 5px 0 0;

            .button {
                float: right;
                padding: 0 !important;
                color: #010101;
                background-color: transparent;
                border: 0;
                vertical-align: middle;
                margin-top: 12px;

                

                &:after{
                    content: "";
                    position: relative;
                    left: 2px;
                    bottom: 3px;
                    display: inline-block;
                    background-color: $default-heading-color;
                    height: 1px;
                    width: 33px;
                    @include eltdfTransition(left .4s ease);
                }

                &:hover{
                    color: #010101;
                    background-color: transparent;
                    &:after{
                        left: -25px;
                    }
                }
            }

            .price_label {
                float: left;
                font-size: 16px;
                line-height: 50px; // to match button height
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images

    &.widget_shopping_cart,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
        ul {
            li {
                min-height: $thumb;
                position: relative;

                a {
                    img {
                        left: 0;
                        max-width: $thumb;
                        position: absolute;
                        top: $widget-list-item-padding;
                        width: 100%;
                    }

                    span {
                        box-sizing: border-box;
                        display: block;
                        padding: 0 0 0 #{$thumb + $inner-padding};
                        position: relative;
                    }
                }

                > *:not(a) {
                    clear: both;
                    float: left;
                    margin: 0 0 0 #{$thumb + $inner-padding};
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images and plain text next to image

    &.widget_shopping_cart,
    &.widget_recent_reviews {
        a {
            display: block;
            padding: 0 0 0 #{$thumb + $inner-padding};

            @include eltdfH5();
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // recent reviews

    //&.widget_recent_reviews {
    //    .reviewer {
    //    }
    //}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // shopping cart

    &.widget_shopping_cart {
        .remove {
            font-size: 0;
            font-weight: normal;
            line-height: 1;
            position: absolute;
            right: 0;
            top: 5px;
            text-decoration: none;

            &:after {
                content: "\4d";
                display: block;
                font-family: 'ElegantIcons';
                font-size: 15px;
                line-height: 15px;
                position: relative;
            }
        }

        .variation {
            @include eltdfRelativeHolderLayout();

            > * {
                display: inline-block;
                margin: 0;
                vertical-align: middle;

                p {
                    margin: 0;
                }
            }
        }

        .total {
            margin: 17px 0 13px;
        }

        .buttons {
            .button {
                @include eltdfButtonDefaultStyle();
                @include eltdfButtonSize();
                @include eltdfButtonSolidColor();

                &:hover {
                    @include eltdfButtonSolidHoverColor();
                }

                &:first-child {
                    float: left;
                }

                &:last-child {
                    float: right;
                }
            }
        }
    }

    &.woocommerce-widget-layered-nav{
        .woocommerce-widget-layered-nav-list{
            li{
                a{
                    font-family: $default-text-font;
                    font-size: 11px;
                    font-weight: 400;
                    line-height: 25px;
                    letter-spacing: 0.3em;
                    text-transform: uppercase;
                }
                .count{
                    font-size: 11px;
                }
            }
        }
    }

}

/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */