/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.eltdf-woo-single-page {
	
	&.eltdf-woo-single-thumb-on-left-side {
		
		.eltdf-single-product-content {
			
			.images {

				@media only screen and (min-width: $ipad-portrait-plus-pixel) {

					figure {
						width: 100%;
						margin: 0;
						overflow: hidden;
					}

					&.woocommerce-product-gallery--with-images {

						.woocommerce-product-gallery__image {

							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								box-sizing: border-box;

								a {
									height: 100%;

									img {
										height: 100%;
									}
								}
							}
						}
					}
				}

				@media only screen and (min-width: $laptop-landscape-large-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
						overflow: hidden;
					}
					
					$thumb_size: 128;
					$space_between_items: 32;
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							$first_thumb_offset: $thumb_size + $space_between_items;
							
							left: #{$first_thumb_offset}px;
							width: calc(100% - #{$first_thumb_offset}px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: #{$thumb_size}px;
								padding: 0 0 #{$space_between_items}px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
						}
					}
					
					$thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
					@for $i from 0 to length($thumb_3_columns_label) {
						&.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
							$column_value: $i+2;
							$column_size: 100 / $column_value;
							$column_width: calc(#{100% / $column_value} + #{$space_between_items / $column_value}px);
							
							.woocommerce-product-gallery__image {
								
								&:not(:first-child) {
									height: $column_width;
								}
								
								$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
								@for $i from 0 to length($thumbnail_numbers) {
									$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
									
									&:nth-child(#{$thumbnail_number_value}#{n}) {
										$first_param: ($column_size * $i)#{'%'};
										$second_param: ($space_between_items / $column_value * $i)#{px};
										$topPos: calc(#{$first_param} + #{$second_param});
										
										top: $topPos;
									}
								}
							}
						}
					}
				}


				@media only screen and (min-width: $laptop-landscape-mac-plus-pixel) and (max-width: $laptop-landscape-large) {

					$thumb_size: 100;
					$space_between_items: 32;

					.woocommerce-product-gallery__image {

						&:first-child {
							$first_thumb_offset: $thumb_size + $space_between_items;

							left: #{$first_thumb_offset}px;
							width: calc(100% - #{$first_thumb_offset}px);
						}
					}

					&.woocommerce-product-gallery--with-images {

						.woocommerce-product-gallery__image {

							&:not(:first-child) {
								width: #{$thumb_size}px;
								padding: 0 0 #{$space_between_items}px;
							}
						}
					}

					$thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
					@for $i from 0 to length($thumb_3_columns_label) {
						&.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
							$column_value: $i+2;
							$column_size: 100 / $column_value;
							$column_width: calc(#{100% / $column_value} + #{$space_between_items / $column_value}px);

							.woocommerce-product-gallery__image {

								&:not(:first-child) {
									height: $column_width;
								}

								$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
								@for $i from 0 to length($thumbnail_numbers) {
									$thumbnail_number_value: nth($thumbnail_numbers, $i+1);

									&:nth-child(#{$thumbnail_number_value}#{n}) {
										$first_param: ($column_size * $i)#{'%'};
										$second_param: ($space_between_items / $column_value * $i)#{px};
										$topPos: calc(#{$first_param} + #{$second_param});

										top: $topPos;
									}
								}
							}
						}
					}
				}

				@media only screen and (min-width: $ipad-landscape-plus-pixel) and (max-width: $laptop-landscape-mac) {

					$thumb_size: 82;
					$space_between_items: 32;

					.woocommerce-product-gallery__image {

						&:first-child {
							$first_thumb_offset: $thumb_size + $space_between_items;

							left: #{$first_thumb_offset}px;
							width: calc(100% - #{$first_thumb_offset}px);
						}
					}

					&.woocommerce-product-gallery--with-images {

						.woocommerce-product-gallery__image {

							&:not(:first-child) {
								width: #{$thumb_size}px;
								padding: 0 0 #{$space_between_items}px;
							}
						}
					}

					$thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
					@for $i from 0 to length($thumb_3_columns_label) {
						&.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
							$column_value: $i+2;
							$column_size: 100 / $column_value;
							$column_width: calc(#{100% / $column_value} + #{$space_between_items / $column_value}px);

							.woocommerce-product-gallery__image {

								&:not(:first-child) {
									height: $column_width;
								}

								$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
								@for $i from 0 to length($thumbnail_numbers) {
									$thumbnail_number_value: nth($thumbnail_numbers, $i+1);

									&:nth-child(#{$thumbnail_number_value}#{n}) {
										$first_param: ($column_size * $i)#{'%'};
										$second_param: ($space_between_items / $column_value * $i)#{px};
										$topPos: calc(#{$first_param} + #{$second_param});

										top: $topPos;
									}
								}
							}
						}
					}
				}

				@media only screen and (min-width: $ipad-portrait-plus-pixel) and (max-width: $ipad-landscape) {

					$thumb_size: 127;
					$space_between_items: 32;

					.woocommerce-product-gallery__image {

						&:first-child {
							$first_thumb_offset: $thumb_size + $space_between_items;

							left: #{$first_thumb_offset}px;
							width: calc(100% - #{$first_thumb_offset}px);
						}
					}

					&.woocommerce-product-gallery--with-images {

						.woocommerce-product-gallery__image {

							&:not(:first-child) {
								width: #{$thumb_size}px;
								padding: 0 0 #{$space_between_items}px;
							}
						}
					}

					$thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
					@for $i from 0 to length($thumb_3_columns_label) {
						&.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
							$column_value: $i+2;
							$column_size: 100 / $column_value;
							$column_width: calc(#{100% / $column_value} + #{$space_between_items / $column_value}px);

							.woocommerce-product-gallery__image {

								&:not(:first-child) {
									height: $column_width;
								}

								$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
								@for $i from 0 to length($thumbnail_numbers) {
									$thumbnail_number_value: nth($thumbnail_numbers, $i+1);

									&:nth-child(#{$thumbnail_number_value}#{n}) {
										$first_param: ($column_size * $i)#{'%'};
										$second_param: ($space_between_items / $column_value * $i)#{px};
										$topPos: calc(#{$first_param} + #{$second_param});

										top: $topPos;
									}
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include eltdfRelativeHolderLayout();
	    float: none;
	    
    }
        
    .eltdf-single-product-content {
        @include eltdfRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 55%;
		    margin: 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include eltdfRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .eltdf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 45%;
        padding: 0 0 0 78px;
        float: left;
        box-sizing: border-box;
	
	    .eltdf-single-product-title {
		    display: block;
		    margin: 0 0 19px;
	    }
	
	    .price {
		    @include eltdfWooCommercePriceStyle();
		    margin: 15px 0 7px;
		    * {
		    	font-size: 18px;
		    }
	    }
	
	    .woocommerce-product-rating {
		    @include eltdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 16px 0 42px;

		    .woocommerce-review-link {
		    	display: none;
		    }
	    }
	
	    .woocommerce-product-details__short-description {
		    @include eltdfRelativeHolderLayout();
		    margin: 15px 0 27px;
		
		    p {
			    margin: 0;
		    }
	    }
	
	    .product_meta {
		    margin: 62px 0 0;
		    font-family: $default-text-font;
		
		    > span {
			    display: block;
			    margin: 3px 0;
			    font-size: 14px;
			
			    a {
				    color: $first-main-color;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
		    span {
		    	a, span {
		    		color: $first-main-color;
		    		text-transform: uppercase;
		    		font-weight: 400;
		    		font-size: 11px;
		    		letter-spacing: 0.3em;
		    	}
		    }
	    }
	
	    .eltdf-woo-social-share-holder {
		    @include eltdfRelativeHolderLayout();
		    margin: 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include eltdfRelativeHolderLayout();
		    margin: 34px 0 0;
	    }
	
	    .eltdf-quantity-buttons {
	        position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    border: 1px solid #d7d7d7;
		    box-sizing: border-box;
		    line-height: 58px;
		    margin: 0 20px 0 0;
		    height: 65px;
		    padding: 0 22px;
		    margin: 0 30px 0 0;
	    }
    }
	
	.woocommerce-tabs {
		@include eltdfRelativeHolderLayout();
		margin: 82px 0 0;
		
		ul.tabs {
			@include eltdfRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			text-align: center;
			clear: both;
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				margin: 0 52px 0 0;

				&:last-child {
					margin: 0px;
				}

				&:after {
							content: '';
							width: 100%;
							height: 1px;
							background-color: $first-main-color;
							opacity: 0;
							display: block;
				}
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 10px 15px;
					color: $first-main-color;
					text-transform: uppercase;
					letter-spacing: 0.3em;
					font-size: 11px;
					font-weight: 400;
					
				}
				&.active {
					
						&:after {
							
							opacity: 1;
						}
					
				}
			}
		}
		
		.entry-content {
			@include eltdfRelativeHolderLayout();
			margin: 0 -400px;
		    padding: 17px 400px 21px;
		    border-top: 1px solid $header-border-color;
		    position: relative;
		    display: inline-block;
		    width: 100%;
		    vertical-align: middle;
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 18px 0 20px;
				text-align: center;
				color: $default-text-color;
				font-family: $default-text-font;
				font-size: 16px;
				line-height: 25px;
				
			}
		}
		
		table {
			margin: 18px 0;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-text-color;
				width: 1%;
				padding: 0 10px 0 0;
				font-size: 14px;
			    font-weight: 300;
			}
			
			td {
				width: 99%;
				font-weight: 400;
				font-size: 11px;
				letter-spacing: .3em;
				text-transform: uppercase;
				color: $first-main-color;
			}
		}
		
		#reviews {
			@include eltdfRelativeHolderLayout();
			margin: 10px 0 30px;
			
			h2 {
				margin: 8px 0 24px;
				line-height: 1.02em;
				letter-spacing: -.015em;
				font-size: 30px;
				font-family: $default-heading-font;
				color: $first-main-color;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include eltdfRelativeHolderLayout();
					
					img {
						float: left;
						width: 70px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 90px;
					
					p {
						margin: 0;
					}
					.woocommerce-review__author {
						font-family: $default-heading-font;
						font-size: 20px;
					}
					.star-rating {
						vertical-align: top;
					}
					.meta {
						top: -2px;
					    position: relative;
					    display: block;
					}
				}
			}
			
			.comment-respond {
				margin-top: 18px;
				
				.comment-reply-title {
					margin: 0 0 10px;
					letter-spacing: -.015em;
					line-height: 1.02em;
					font-size: 30px;
					font-family: $default-heading-font;
					color: $first-main-color;
				}
				
				p {
					margin: 0;
				}
				
				.stars {
					overflow: hidden;
					
					a {
						position: relative;
						display: inline-block;
						
						font-size: 0;
						margin: 0;
						@include eltdfWooCommerceSingleProductRatingsStyle();
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 36px 0 0;
		
		> h2 {
			display: block;
			margin: 37px 0 32px;
			text-align: center;
			font-size: 30px;
		}
		
		.product {
			
			.eltdf-pl-text-wrapper {
				margin-bottom: 0;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */