/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.eltdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    margin: 0 10px;

    .touch & {
        cursor: pointer;
    }

    .eltdf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .eltdf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;
        line-height: 1;

        .eltdf-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
        }
        .eltdf-cart-number {
            position: absolute;
            top: 48px;
            right: -10px;
            line-height: 1.4em;
            text-align: center;
            font-size: 10px;
            color: $default-heading-color;
            font-weight: 400;
        }

        &.eltdf-header-cart-icon-pack {
            
            .eltdf-cart-icon {
                font-size: 20px;
                line-height: 1em;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.eltdf-header-cart-svg-path {
            
            .eltdf-cart-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }

    &:hover {

        .eltdf-shopping-cart-dropdown {
			visibility: visible;
			max-height: 100vh;
			opacity: 1;
			@include eltdfTransition(max-height 0.6s cubic-bezier(.53,.01,.18,1), opacity 0s ease);
        }
    }
}

.eltdf-dark-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.eltdf-light-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.eltdf-shopping-cart-dropdown {
    position: absolute;
	top: 100%;
	left: auto;
    right: -50px;
    width: 401px;
    background-color: $default-heading-color;
    border: 1px solid $default-border-color;
    text-align: left;
    z-index: 1000;
	max-height: 0;
	opacity: 0;
	visibility: hidden;
	overflow: hidden;
    box-sizing: border-box;
	@include eltdfTransition(opacity 0.2s, max-height 0s linear 0.15s);

    ul {
        padding: 0;
        margin: 30px 0 29px;

        li {
            @include eltdfTableLayout();
            padding: 0 30px 15px;
            box-sizing: border-box;
            border-bottom: 1px solid transparent;

            &.eltdf-empty-cart {
                color: #fff;
				font-size: 11px;
				font-weight: 400;
                letter-spacing: .3em;
				text-transform: uppercase;
                border-bottom: 0;
            }

       
            &:last-child {
                padding-bottom: 0;
                border-top: 1px solid $first-main-color;
            }
        }
    }

    .eltdf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 80px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .eltdf-item-info-holder {
        @include eltdfTableCellLayout();
        vertical-align: top;
        padding-left: 22px;

        .eltdf-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 5px 0;
            box-sizing: border-box;
            color: #fff;
            a {
                &:hover {
                    color: #fff;
                }
            }
        }

        .remove {
            position: absolute;
            top: -1px;
            right: 0;
	        font-size: 22px;
	        line-height: 1;
            color: #fff;

            span {
                display: block;
                line-height: inherit;
                color: #fff;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .eltdf-quantity {
            display: block;
            margin: 3px 0 0;
            font-family: $default-text-font;
            font-size: 11px;
            text-transform: uppercase;
            letter-spacing: 0.3em;
            color: #fff;
        }

        .amount {
            display: block;
            margin: 3px 0 7px;
            font-family: $default-text-font;
            font-size: 16px;
            color: #7e7e7e;
        }
    }

    .eltdf-cart-bottom {
         padding: 0 30px 30px;
         margin: 14px 0 0;

        .eltdf-btn-holder {
            display: inline-block;
            float: left;
        }

		.eltdf-btn-holder:nth-child(2){
			margin-right: 29px;
		}

        .eltdf-view-cart,
		.eltdf-checkout {
            @include eltdfRelativeHolderLayout();
            height: 60px;
            line-height: 58px;
            letter-spacing: 0.3em;
            padding: 0 36px;
            font-family: $default-text-font;
            font-size: 10px;
            font-weight: 400;
            text-transform: uppercase;
            text-align: center;
            box-sizing: border-box;
            @include eltdfTransition(color .2s ease-out, border-color .2s ease-out, background-color .2s ease-out);

            span {
                display: block;
            }
        }

		.eltdf-view-cart{
			border: 1px solid #fff;
			color: #fff;
			background-color: transparent;

			&:hover {
				color: $first-main-color;
				background-color: #fff;
			}
		}

        .eltdf-checkout {
			color: $first-main-color;
			background-color: #fff;
			border: 1px solid transparent;

			&:hover {
				color: #fff;
				background-color: transparent;
				border-color: #fff;
			}
        }

        .eltdf-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 24px 0 18px;

            > * {
                display: table-cell;
                vertical-align: top;
                color: $default-heading-color;
                line-height: 20px;
            }
            .eltdf-total{
                font-family: $default-text-font;
                font-size: 16px;
                font-weight: 400;
                color: $default-text-color;
            }

            .eltdf-total-amount {
                text-align: right;
	            font-size: 16px;
                color: $default-text-color;
            }

        }
    }
}

.eltdf-vertical-menu-area {

    .eltdf-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */