/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.eltdf-pl-holder {
    @include eltdfRelativeHolderLayout();
	
	&.eltdf-info-on-image {
		
		.eltdf-pli {
			
			.eltdf-pli-add-to-cart {
				margin: 20px 0 0;
			}
			.eltdf-pli-text {
				background-color: rgba($default-heading-color, 0.6);
			}

			.eltdf-pli-rating-holder{
				margin: 0 0 3px;
			}
			.eltdf-pli-inner{
				.eltdf-pli-text-inner{
					vertical-align: middle;
					padding-bottom: 0;
				}
			}

		}
	}

    .eltdf-pli {
        padding: 0;
        z-index: 100;

	    .eltdf-pli-title {
		    margin: 0 0 4px;
	    }
	
	    .eltdf-pli-category {
		    margin: 11px 0 3px;
	    }

        .eltdf-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }

	    .eltdf-pli-rating-holder {
		    @include eltdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 10px 0 3px;
	    }

	    .eltdf-pli-rating {
		    @include eltdfWooCommerceRatingsStyle();
	    }

        .eltdf-pli-price {
	        @include eltdfWooCommercePriceStyle();
        }
    }

    .eltdf-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        &:hover {

            .eltdf-pli-text {
                opacity: 1;
            }
        }

        .touch & {
            cursor: pointer;
        }

        .eltdf-pli-image {
            @include eltdfRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }

            .eltdf-pli-onsale,
            .eltdf-pli-out-of-stock,
            .eltdf-pli-new-product {
	            @include eltdfWooCommerceMarksGeneralStyle();
            }

            .eltdf-pli-onsale {
	            @include eltdfWooCommerceSaleStyle();
            }

            .eltdf-pli-out-of-stock {
	            @include eltdfWooCommerceOutOfStockStyle();
            }
	
	        .eltdf-pli-new-product {
		        @include eltdfWooCommerceNewProductStyle();
	        }
        }
            
        .eltdf-pli-text {
            @include eltdfAbsoluteHolderLayout();
            padding: 10px;
            opacity: 0;
            box-sizing: border-box;
            @include eltdfTransition(opacity .2s ease-out);

            
        }


        .eltdf-pli-text-outer {
	        @include eltdfTableLayout();
	        text-align: center;
        }

        .eltdf-pli-text-inner {
            @include eltdfTableCellLayout();
            vertical-align: bottom;
            padding-bottom: 40px;

            .eltdf-pli-title,
            .eltdf-pli-category a,
            .eltdf-pli-excerpt,
            .eltdf-pli-rating,
            .eltdf-pli-price {
                color: #fff;
            }
	
	        .eltdf-pli-category a:hover {
		        color: rgba(#fff, .8);
	        }

	        .eltdf-pli-add-to-cart {

		        &.eltdf-default-skin {

			        .button,
			        .added_to_cart {
				        @include eltdfButtonSolidColor();

				        &:hover {
				            @include eltdfButtonSolidHoverColor();
				        }
			        }
		        }

		        &.eltdf-light-skin {

			        .button,
			        .added_to_cart {
				        color: $default-heading-color;
				        background-color: #fff;

				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
		
		        &.eltdf-dark-skin {
			
			        .button,
			        .added_to_cart {
				        color: #fff;
				        background-color: $default-heading-color;
				
				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
	        }

            .button,
            .added_to_cart {
	            @include eltdfButtonDefaultStyle();
	            @include eltdfButtonSize();
	            white-space: nowrap;
	            z-index: 3;
            }

            .button.added {
                display: none;
            }
        }

	    .eltdf-pli-link {
		    @include eltdfAbsoluteHolderLayout();
	    }
    }

    .eltdf-pli-text-wrapper {
        @include eltdfRelativeHolderLayout();
        margin: 30px 0 28px;
        text-align: center;
    }
	
	&.eltdf-product-info-light {
		
		.eltdf-pli-inner {
			
			.eltdf-pli-text-inner {
				
				.eltdf-pli-title,
				.eltdf-pli-category a,
				.eltdf-pli-excerpt,
				.eltdf-pli-rating,
				.eltdf-pli-price {
					color: #fff;
				}
				
				.eltdf-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.eltdf-product-info-dark {
		
		.eltdf-pli-inner {
			
			.eltdf-pli-text-inner {
				
				.eltdf-pli-title,
				.eltdf-pli-category a,
				.eltdf-pli-excerpt,
				.eltdf-pli-rating,
				.eltdf-pli-price {
					color: $default-heading-color;
				}
				
				.eltdf-pli-category a:hover {
					color: rgba($default-heading-color, .8);
				}
			}
		}
	}
	
	/*************** Masonry Layout - begin ***************/
	
	&.eltdf-masonry-layout {
		
		.eltdf-pli-inner {
			width: 100%;
			height: 100%;
			
			.eltdf-pli-image {
				height: 100%;
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */