/* ==========================================================================
   Product Pair shortcode styles - begin
   ========================================================================== */

.eltdf-pp-holder {
	margin: 0 auto;
    position: relative;
	display: table;

	&.eltdf-big-product-first{
		direction: rtl;
	}

	.eltdf-ppi {
		display: table-cell;
		vertical-align: middle;
		padding: 0 15px;

		.eltdf-ppi-inner{
			.eltdf-ppi-image {
				position: relative;
				overflow: hidden;
				img {
					display: block;
				}
			}
			.eltdf-ppi-text {
				position: absolute;
				bottom: 0;
				left: 50%;
				@include eltdfTransform(translateX(-50%));

				.eltdf-ppi-text-inner {
					@include eltdfTransition(all .2s);
					@include eltdfTransform(translateY(100%));

					.eltdf-yith-wcqv-holder {

						a {
							position: relative;
							display: inline-block;
							background-color: $first-main-color;
							color: #fff;
							padding: 1px 15px 0;
							font-size: 10px;
							font-weight: 400;
							height: 25px;
							line-height: 25px;
							text-transform: lowercase;
							letter-spacing: .1em;

							&:hover {
								color: #fff;
							}
						}
					}
				}
			}

			.eltdf-ppi-text-wrapper {
				position: relative;
				padding-top: 29px;
				text-align: center;

				.eltdf-ppi-title {
					margin: 0;
					display: block;
				}

				.eltdf-ppi-price-wrapper {
					position: relative;
					width: 40%;
                    margin-top: 7px;
					overflow: hidden;
					display: inline-block;
				}

				.eltdf-ppi-price {
					margin: 0;
					position: relative;
					display: inline-block;
					@include eltdfTransform(translateX(0));
					@include eltdfTransition(all .2s);

					ins{
						text-decoration: none;
					}

				}

				.eltdf-ppi-category {
					margin: 3px 0 0;
					padding: 0;
					float: left;
					position: relative;
					clear: both;
					line-height: 1;

					a{
						font-size: 12px;
						color: #949494;
						text-transform: lowercase;
					}
				}

				.eltdf-eltdf-small-product-add-to-cart,
				.eltdf-eltdf-big-product-add-to-cart {
					position: absolute;
					top: 0;
					left: 0;
					text-transform: lowercase;
					opacity: 0;
					@include eltdfTransform(translateX(-50%));
					@include eltdfTransition(all .4s cubic-bezier(0, 0.95, 0.77, 1));

					a {
						position: relative;
						right: 0;
                        top: -2px;
						white-space: nowrap;
						background-color: transparent;
						padding: 0;
						margin: 0;
						font-family: $default-text-font;
						font-size: 11px;
						line-height: 2.8em;
						letter-spacing: 0.3em;
						text-transform: uppercase;

						&:hover {
							color: $first-main-color;
						}

                        &.added {
                            display: none;
                        }
					}
				}
			}

			.eltdf-ppi-trim {
				position: absolute;
				top: -2px;
				left: -2px;
				height: calc(100% - 36px);
				width: calc(100% - 36px);
				border: 20px solid #fff;
				@include eltdfTransform(scale(1.2));
				visibility: hidden;
				-webkit-transition: -webkit-transform .7s cubic-bezier(.47,.56,0,.88), visibility .0s .7s;
				transition: transform .7s cubic-bezier(.47,.56,0,.88), visibility .0s .7s;
			}

			&:hover {
				.eltdf-ppi-text-wrapper {

					.eltdf-ppi-price{
						opacity: 0;
						@include eltdfTransform(translateX(100%));
					}

					.eltdf-eltdf-small-product-add-to-cart,
					.eltdf-eltdf-big-product-add-to-cart{
						opacity: 1;
						left: 50%;
						@include eltdfTransform(translateX(-50%));
					}
				}

				.eltdf-ppi-text-inner {
					@include eltdfTransform(translateY(0));
				}

				.eltdf-ppi-trim {
					visibility: visible;
					@include eltdfTransform(scale(1.01));
					-webkit-transition: -webkit-transform .7s cubic-bezier(.47,.56,0,.88);
					transition: transform .7s cubic-bezier(.47,.56,0,.88);
				}
			}

		}
	}

}
/* ==========================================================================
   Product Pair shortcode styles - end
   ========================================================================== */